.start-element{
}
.game-title{
  text-align: center;
  font: normal 5vw fantasy;
  padding-top: 10vh;
  color: #338899;
}
.start-button{
  margin-top: 10vh;
  margin-left: 30vw;
  width: 40vw;
  height: 10vh;
  position: absolute;
  background: linear-gradient( #aaddff, #8ab0f2);
  font-size: 5vw;
  color: #eeffff;
  border: 0;
  -webkit-filter:drop-shadow(2px 3px 3px rgba(0, 0, 0, 0.2));
  -moz-filter:drop-shadow(2px 3px 3px rgba(0, 0, 0, 0.2));
  -ms-filter:drop-shadow(2px 3px 3px rgba(0, 0, 0, 0.2));
  filter:drop-shadow(2px 3px 3px rgba(0, 0, 0, 0.2));
}
.game-caution{
  width: 60vw;
  height: 15vh;
  max-height: 200px;
  overflow-y: scroll;
  margin-top: 30vh;
  margin-left: 18vw;
  padding: 3vw;
  padding-right: 2vw;
  border: 0;
  background-color: rgba(230, 240, 250, 0.6);
  font-size: 2.2vw;
  color: #338899;
  white-space: pre-line;
}
.credit{
  position: absolute;
  margin-top: 10vh;
  margin-left: 20vw;
  font-size: 2vw;
  color: #ccdede
}

.player-name-input{
  margin: 10vw 2vw 3vw 6vw;
}
form label{
  margin-left: 3vw;
  font-size: 3vw;
  font-weight: lighter;
  color: #aaa;
}
form input{
  width: 60vw;
  padding: 2vw;
  margin-left: 5vw;
  font-size: 3vw;
  border: none;
  border-bottom: 0.2vw solid #aaa;
  background-color: #f5fbfb;
  box-sizing: border-box; /*padding と borderをboxの幅と高さに含める(＝親要素をはみ出なくなる)*/
}

form input:focus{
  box-shadow: 0 0 1.2vw #338899;
  border-bottom: 0.2vw solid #f5fbfb; /*border 非表示*/
  transition: all 0.3s;
  outline: 0;
}